
.compo-description-menu {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	align-items: center;

	height: 650px;

	background: #143291;

}

	.compo-description-menu__elements-block {
		display: flex;
		flex-direction: row;
		justify-content: center;
		align-items: center;

		margin-top: 40px;
		margin-bottom: 30px;

		height: 65%;
		width: 100%;
	}

		.compo-description-menu__elements-block	 div {
			display: flex;
			flex-direction: column;
			justify-content: flex-start;
			align-items: center;

			border-radius: 8px;

			height: 300px;
			width: 200px;

			margin-left: 20px;
			margin-right: 20px;
			
			padding: 15px;

			background-color: #FFFFEF;
		}

			.compo-description-menu__elements-block div img {
				height: 100px;
				width: 100px;
			}

			.compo-description-menu__elements-block div p {
				font-size: 1.25em;
				text-align: center;
			}

		.compo-description-menu a {
			border-radius: 10px;

			padding: 20px;
			margin-bottom: 50px;

			font-weight: bold;
			font-size: 2em;
			text-decoration: none;

			background-color: #222222;
			color: white;
		}

			.compo-description-menu a:hover {
				background-color: #333333;
			}

/* Versions petits écrans */
@media (max-width: 800px) {
	.compo-description-menu {
		height: fit-content;
	}

	/* Les éléments s'alignent maintenant en colonne */
	.compo-description-menu__elements-block {
		flex-direction: column;
	}

	.compo-description-menu__elements-block div {
		height: 200;
		width: 65%;

		margin-bottom: 50px;
	}
}