
.compo-main-cover-page {
	display: flex;
	flex-direction: column;
	justify-content: flex-end;
	align-items: center;

	height: 100vh;

	background-image: url("../images/bg_mountain_1.jpg");
	background-position: center;
}

	.compo-main-cover-page p {
		width: 1000px;

		font-size: 8em;
		color: white;
		text-align: center;
		font-weight: bold;
	}

	.compo-main-cover-page a {
		height: 30px;

		padding-bottom: 20px;
	}

	.compo-main-cover-page img {
		height: 100%;

		padding-bottom: 20px;
	}

/* Versions pour petits écrans */
@media ( max-width: 1100px ) {
	.compo-main-cover-page p {
		width: 600px;

		font-size: 6em;
	}
}

@media ( max-width: 700px ) {
	.compo-main-cover-page p {
		width: 400px;
		
		font-size: 3em;
	}
}