
.compo-navbar-main {
	display: flex;
	flex-direction: row;
	justify-content: space-between;
	align-items: center;

	height: 80px;
	width: 100%;

	position: fixed;

	background-color: #222222;
}
	
	/* Partie logo */
	.compo-navbar-main__link-logo {

		width: 180px;

		padding-left: 20px;
	}

	/* Partie liens */
	.compo-navbar-main__box-links {
		display: flex;
		flex-direction: rox;
		justify-content: space-around;
		align-items: center;

		width: 100%;
	}

		.compo-navbar-main__box-links a {
			font-size: 1.25em;
			color: white;
			text-decoration: none;
		}

	/* Partie profil */
	.compo-navbar-main__link-profil {

		width: 50px;

		padding-top: 10px;
		padding-right: 20px;
	}

/* Versions petits écrans */
@media ( max-width: 900px ) {

	.compo-navbar-main__box-links {
		display: none;
	}
}
